/*
 * A Textfield that allows the user to enter the list of edges.
 * Also contains the buttons that the user will use to interact with the app.
 */

import React, {Component} from 'react';

/**
 * Props:
 *
 * onChange - a listener for when the edge text area has a keyboard event
 * value - the value to display in the text area
 */
class EdgeList extends Component {

    onDraw () {
        let parsedEdges = [];

        // Parse each edge by newline to an array
        let edgeListContent = this.props.value.split(/\r?\n/);

        // Remove any empty elements (i.e. from extra newlines)
        edgeListContent = edgeListContent.filter(Boolean);

        let hasError = false;

        // Parse each of the edge entries by line
        for (let edge of edgeListContent) {

            // Parse each line by space
            edge = edge.split(' ');

            hasError = this.checkFormat(edge);
            if (hasError) {
                break;
            }

            // Parse the coordinates as [x1,y1] [x2,y2]
            edge[0] = edge[0].split(',');
            edge[1] = edge[1].split(',');

            // Parse all edge properties individually. If the user
            // enters decimals, parseFloat will be able to catch
            // that rather than using parseInt, which will just
            // end up rounding the coordinates to integers.
            let x1 = parseFloat(edge[0][0]);
            let y1 = parseFloat(edge[0][1]);
            let x2 = parseFloat(edge[1][0]);
            let y2 = parseFloat(edge[1][1]);
            let color = edge[2];
            
            hasError = this.checkCoordinates(x1, y1, x2, y2);
            if (hasError) {
                break;
            }
            parsedEdges.push([x1, y1, x2, y2, color]);
        }

        if (!hasError) {
            this.props.onDraw(parsedEdges);
        }
    }

    // Verifies proper format.
    checkFormat = (input) => {
        if ((input.includes(null)) || (input.length !== 3)) {
            alert("Edges must be entered in the form: x1,y1 x2,y2 color");

            return true;
        }
    };

    // Verifies coordinate values are non-negative integers
    // within [0, gridSize - 1].
    checkCoordinates = (x1, y1, x2, y2) => {
        let maxSize = this.props.size - 1;
        if ((x1 > maxSize || x1 < 0 || !Number.isInteger(x1)) ||
            (y1 > maxSize || y1 < 0 || !Number.isInteger(y1)) ||
            (x2 > maxSize || x2 < 0 || !Number.isInteger(x2)) ||
            (y2 > maxSize || y2 < 0 || !Number.isInteger(y2))) {

            alert("With a grid size of " + this.props.size +
                ", all coordinates must be integers from 0 to "
                + maxSize + " (inclusive).");

            return true;
        }
    };

    render() {
        let size = parseInt(this.props.size);
        let output = `With a grid size of ${size}, all coordinates
        must be integers from 0 to ${size - 1} (inclusive).`;
        if (isNaN(size) || size === 0 || size === 1) {
            output = "The grid size must be greater than 1 to draw lines.";
        }

        return (
            <div id="edge-list">
                <span id="bodyText">Draw lines between the dots by specifying the line coordinates
                in the box below (upper-left dot is x = 0, y = 0). Separate each
                    individual line with ENTER.</span><br/>
                <p style={{color: '#98959d'}}>{output}</p>
                <textarea
                    ref = 'textarea'
                    placeholder="Enter lines here: x1,y1 x2,y2 color"
                    rows={5}
                    cols={40}
                    onChange={this.props.onChange}
                    value={this.props.value}
                />
                <br/>

                <button onClick={() => {

                    // Draws if the size is greater than 1.
                    if (this.props.size > 1) {
                        this.onDraw();
                        console.log('Draw onClick');
                    }
                }}>Draw Lines</button>

                <button onClick={() => {
                    this.props.onClear();
                    console.log('Clear onClick');
                }}>Clear Lines</button>

            </div>
        );
    }
}

export default EdgeList;
