import React, {Component} from 'react';
import EdgeList from "./EdgeList";
import Grid from "./Grid";
import GridSizePicker from "./GridSizePicker";
import "./App.css";

// CONSTANTS: default grid size, maximum grid size,
// canvas size in pixels
const DEFAULT_SIZE = 4;
const MAX_SIZE = 200;
const CANVAS_SIZE = 500;

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            gridSize: DEFAULT_SIZE, // # dots in the grid (default: 4)
            gridSizeInput: DEFAULT_SIZE,
            edgeInput: "",          // Content in EdgeList
            parsedEdges: "",        // Edges parsed from EdgeList
            drawEdges: false        // Switched when draw button is triggered
        };

        // Used for calling Grid component
        this.gridReference = React.createRef();
    }

    updateGridSize = (event) => {
        const newValue = event.target.value;

        this.setState({
            gridSizeInput: parseFloat(newValue)
        });

        // Checks that grid size is within [0,200].
        if ((Number.isInteger(parseFloat(newValue))) &&
            ((newValue) >= 0) && (newValue <= MAX_SIZE)) {

            this.setState({
                gridSize: parseInt(newValue)
            })

          // If the input is empty and isNaN the grid becomes empty.
        } else if (isNaN(parseInt(newValue))){
            this.setState({
                gridSize: ""
            })
        }
    };

    onEdgeListChange = (event) => {
        this.setState({
            edgeInput: event.target.value,
        });
    };

    // When draw is clicked, parse the edges and pass to Grid.
    onDrawClicked = (parsedEdges) => {
        this.setState({
            parsedEdges: parsedEdges,
            drawEdges: !this.state.drawEdges
        });
    };

    onClearClicked = () => {
        this.setState({
            // Clear parsed edges
            parsedEdges: "",

            // Clear the EdgeList box
            edgeInput: ""
        });

        // Calls Grid.js eraseEdges
        this.gridReference.current.eraseEdges();

        console.log(this.state.parsedEdges);
    };

    render() {
        return (
            <div id="wrapper">
                <p id="app-title">connect the dots</p>
                <GridSizePicker
                    value={this.state.gridSizeInput}
                    onChange={this.updateGridSize}
                />

                <Grid
                    ref={this.gridReference}
                    edges={this.state.parsedEdges}
                    toggleDrawEdges={this.state.drawEdges}
                    size={this.state.gridSize}
                    width={CANVAS_SIZE}
                    height={CANVAS_SIZE}
                />

                <EdgeList
                    value={this.state.edgeInput}
                    size={this.state.gridSize}
                    onChange={this.onEdgeListChange}
                    onDraw={this.onDrawClicked}
                    onClear={this.onClearClicked}
                />
            </div>
        );
    }

}

export default App;