/* A simple grid with a variable size */

import React, {Component} from 'react';

/**
 * Props:
 *
 * width - the desired width of the grid area
 * height - the desired height of the grid area
 * size - the number of points along a single axis in the grid
 * edgeInput - the edge input received from EdgeList to parse and draw
 */

// CONSTANTS: canvas size in pixels, default circle radius
const CANVAS_SIZE = 500;
const DEFAULT_RADIUS = 4;

class Grid extends Component {
    constructor(props) {
        super(props);
        this.state = {
            drawEdges: this.props.toggleDrawEdges,
            edgesToDraw: []
        };

        this.canvasReference = React.createRef();
    }

    componentDidMount() {
        this.redraw();
    }

    componentDidUpdate() {
        this.redraw();
    }

    redraw = () => {
        // Set up the canvas area.
        let ctx = this.canvasReference.current.getContext('2d');
        ctx.clearRect(0, 0, this.props.width, this.props.height);
        ctx.fillStyle = "#272629";
        ctx.fillRect(0,0,500,500);

        // Draw all the dots.
        let coordinates = this.getCircleCoordinates();
        for(let coordinate of coordinates) {
            this.drawCircle(ctx, coordinate);
        }

        // Trigger 'Draw' button call with boolean.
        if ((this.state.drawEdges !== this.props.toggleDrawEdges)) {
            const edges = this.props.edges;

            // Toggle button call.
            this.setState({
                drawEdges: !this.state.drawEdges,
                edgesToDraw: edges
            })
        }

        // Passing the spacing as a parameter allows the edges to
        // be redrawn dynamically as grid size is updated.
        const spacing = CANVAS_SIZE / (this.props.size + 1);
        const maxFit = spacing * this.props.size;
        this.drawEdges(ctx, spacing, maxFit);
    };

    getCircleCoordinates = () => {
        const size = this.props.size;
        const grid = [];
        const spacing = CANVAS_SIZE / (size + 1);

        // Increment x
        for (let i = 1; i <= size; i++) {
            // Increment y
            for (let j = 1; j <= size; j++) {
                grid.push([i * spacing, j * spacing]);
            }
        }
        return grid;
    };

    drawCircle = (ctx, coordinate) => {
        ctx.fillStyle = "white";

        // Generally use a radius of 4, but when there are lots of
        // dots on the grid (> 50) we slowly scale the radius down so
        // they'll all fit next to each other.
        let radius = Math.min(DEFAULT_RADIUS, 100 / this.props.size);

        ctx.beginPath();
        ctx.arc(coordinate[0], coordinate[1], radius, 0,
            2 * Math.PI);
        ctx.fill();
    };

    drawEdges = (ctx, spacing, maxFit) => {
        for (let edge of this.state.edgesToDraw) {
            console.log(edge);

            // Translate dot coordinates to canvas coordinates.
            const x1 = (edge[0] + 1) * spacing;
            const y1 = (edge[1] + 1) * spacing;
            const x2 = (edge[2] + 1) * spacing;
            const y2 = (edge[3] + 1) * spacing;
            const color = edge[4];

            // Only display edges that would fit on the grid.
            if (x1 <= maxFit && y1 <= maxFit &&
                x2 <= maxFit && y2 <= maxFit) {

                // Draw the edges.
                ctx.beginPath();
                ctx.strokeStyle = color;
                ctx.lineWidth = 3;
                ctx.moveTo(x1, y1);
                ctx.lineTo(x2, y2);
                ctx.stroke();
                ctx.closePath();
            }
        }
    };

    // Called from App.js onClearClicked: clears the drawn
    // edges on the canvas.
    eraseEdges = () => {
        this.setState({
            edgesToDraw: []
        })
    };

    render() {
        return (
            <div id="grid">
                <canvas
                    ref={this.canvasReference}
                    width={this.props.width}
                    height={this.props.height}/>
            </div>
        );
    }
}

export default Grid;
