/* A simple TextField that only allows numerical input */

import React, {Component} from 'react';

/**
 * Props:
 *
 * onChange - a listener for when the size text area has a keyboard event
 * value - the value to display in the text area
 */

// CONSTANT: maximum grid size
const MAX_SIZE = 200;

class GridSizePicker extends Component {

    render() {
        // Produces message for the user about the grid size.
        let size = parseFloat(this.props.value);
        let output = `The current grid size is ${size} by ${size}.`;
        let color = 'indianred';

        if (size > MAX_SIZE) {
            output = `Invalid input. The maximum grid size is ${MAX_SIZE}.`;
        } else if (size < 0) {
            output = "Invalid input. The grid size must be at least 0.";
        } else if (size === 0 || isNaN(size)) {
            output = "The grid is currently empty. Increase the grid size!";
            color = '#98959d';
        } else if (!Number.isInteger(size)) {
            output = "Invalid input. The grid size must be an integer.";
        } else {
            color = '#98959d';
        }

        return (
            <div id="grid-size-picker">
                <label>
                    <span id="bodyText">Set the grid size to</span>
                    <input
                        type="number"
                        step={1}
                        value={this.props.value}
                        onChange={this.props.onChange}
                        min={1}
                        max={MAX_SIZE}
                    />
                    <p style={{color: color}}>{output}</p>
                </label>
            </div>
        );
    }
}

export default GridSizePicker;
